.hero {
    background: var(--primary-color);
    color: var(--light-gray-color);
    margin-bottom: 4rem;
    font-family: "Markazi";
    padding: 1rem;
}

.heroContainer {
    max-width: 1024px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
}

.heroImg {
    height: 28rem;
    width: 80%;
    margin-bottom: -4.5rem;
    justify-self: end;
}

.heroImg img {
    height: 100%;
    width: 100%;
    border-radius: 1rem;
}

.heroImg, .heroAction {
    margin-top: 2rem;
}

.heroAction h2 {
    font-size: 2.5rem;
    color: var(--secondary-color);
}

.heroAction h3 {
    font-size: 2rem;
}

.heroText {
    margin: 2rem 0;
    font-size: 1.1rem;
    line-height: 1.3;
}

@media (min-width:768px) and (max-width:1024px) {
    .heroImg {
        height: 24rem;
        width: 90%;
        justify-self: center;
    }
}

@media (min-width:540px) and (max-width:768px) {
    .heroContainer {
        grid-template-columns: 1fr;
    }
    .heroImg {
        height: 28rem;
        width: 70%;
        margin-bottom: 0;
        justify-self: center;
    }
}

@media (max-width:540px) {
    .heroContainer {
        grid-template-columns: 1fr;
    }
    .heroImg {
        height: 28rem;
        width: 100%;
        margin-bottom: 0;
    }
}
